import React from "react";
import AnswerModal from "./AnswerModal";
import { MathHelper } from "../utils";
import './Quiz.css'
import "animate.css"
import bar from "../assets/bar.png";
import circle from "../assets/circle.png";
import rec from "../assets/rectangle.png";
import Options from "./Options";
import tri from "../assets/triangle.png";


const queryParams = new URLSearchParams(window.location.search);
const limit = queryParams.get('limit') ? queryParams.get('limit') : 5;
 

class Quiz extends React.Component {
  _isMounted = false;
  _secondsIntervalRef;
  state = {
    problem: "",
    firstNumber: 0,
    secondNumber: 0,
    symbol: "",
    answer: 0,
    modal: "",
    modalShowing: false,
    streaks: 0,
    totalProblems: 0
  };

  earnLife = () => {
    this.props.onEarnLife();
    this.showModal("success", "STREAK!! You won a life ♥");
    this.setState({
      streaks: 0
    });
  };

  correctAnswer = () => {
    if (this.state.streaks > 2) {
      this.earnLife();
    } else {
      this.showModal("success");
    }

    this._isMounted && this.props.onCorretAnswer();
    this.setState(state => {
      return {
        streaks: state.streaks + 1
      };
    });

    this.nextProblem();
  };

  componentDidMount() {
    this._isMounted = true;
    this.getProblem();

    // this.answerInput.focus();
  }

  shouldComponentUpdate(nextProps) {
    if (this.props.lifes < 1) {
      this.props.onEndGame(this.state.points);
      return false;
    }
    return nextProps.lifes > -1;
  }

  componentWillUnmount() {
    this._isMounted = false;
  }
  componentDidUpdate() {
    if (this.state.totalProblems > limit) {
      this.props.onEndGame(this.state.points)
    }
  }

  wrongAnswer = () => {
    this._isMounted && this.props.onWrongAnswer();
    this.setState({
      streaks: 0
    });
    this.showModal("error");
    this.nextProblem();
  };

  nextProblem = () => {
    setTimeout(() => {
      this.getProblem();
      this._isMounted &&
        this.setState({
          modalShowing: false,
          totalProblems: this.state.totalProblems + 1
        });
      if (this.props.lifes > 0) (this.answerInput && this.answerInput.focus());
    }, 2500);
  };

  evaluateProblem = (attemptedAnswer) => {

    if (attemptedAnswer == this.state.answer) {
      return this.correctAnswer();
    }
    return this.wrongAnswer();
  };

  keyingUp = ev => {
    if (ev.key === "Enter") {
      this.evaluateProblem();
    }
    const val = ev.target.value;
    this.setState({
      answer: Number(val.match(/((-?)\d+)/g)) // accept just numbers and the minus symbol
    });
  };

  showModal = (type, text) => {
    this.setState({
      modal: <AnswerModal type={type} text={text} />,
      modalShowing: true
    });
  };

  getProblem = () => {
    //const newProblemSet = MathHelper.generateAdditionProblem(this.props.points);
    let a  = Math.floor(Math.random() * 10)
    this._isMounted &&
      this.setState({
        //problem: newProblemSet.problem,
        firstNumber: a,
        secondNumber: a+1,
        thirdNumber : a+3,
       // symbol: newProblemSet.symbol,
        answer:  a + 2,
      });
    // let a  = Math.floor(Math.random() * 10)
    // this.setState({
    //   answer: a+2,
    // })
  };



  render() {
  

    return (
      <section className="show-up" style={{ width: "100%", height: "100vh" }}>
        
       
        <div >
          {this.state.modalShowing ? (
            this.state.modal
          ) : (
            <div>
              <div>
               
                <div style={{ borderBottom: "1px solid #aaa", fontSize: "1.5em", marginTop: "30vh", marginLeft: "15%", width: "100%" }}> 
                <h3>find the Missing Number</h3>
                  <div  class = "imgdiv "style={{ display: "flex", marginLeft: "3vw" }}>
                     {/* <button > {this.state.firstNumber} </b>
                    <div >{[...Array(parseInt(this.state.firstNumber))].map((e, i) => {
                      return <img key={i} src={bar} style={{ width: "100%", maxWidth: "3vw" }} draggable="false" />
                    })}</div>  */}
                    <button>{this.state.firstNumber}</button>
                    <button>{this.state.secondNumber}</button>
                    <button>?</button>
                     <button>{this.state.thirdNumber}</button>
                    


                  </div>

                  {/* <div style={{ display: "flex" }}>
                    <div > {this.state.symbol} {this.state.secondNumber} </div>
                    <div  >{[...Array(parseInt(this.state.secondNumber))].map((e, i) => {
                      return <img key={i} src={bar} style={{ width: "100%", maxWidth: "3vw" }} draggable="false" />
                    })}</div>
                  </div> */}
                </div>
                {this._isMounted && <Options handleClick={(ans) => this.evaluateProblem(ans)} correctAnswer={this.state.answer} />}
                
              </div>
            </div>
          )}
        </div>
      </section >

    );
  }
}

export default Quiz;
