import React, { useEffect } from 'react'

export default function Options(props) {
    const [choices, setChoices] = React.useState([])

    useEffect(() => {
        const tempArr = [...Array(parseInt(4))].map((value, index) => {
            return getRandomOption(props.correctAnswer)
        })
        const randomIndex = getNumber(0, tempArr.length)
        tempArr[randomIndex] = props.correctAnswer
        setChoices(tempArr)
    }, [])

    const getRandomOption = (answer) => {
        const randomOption = Math.floor(Math.random() * 10)
        if (randomOption == answer) {
            return getRandomOption(answer)
        }
        return randomOption
    }
    const getNumber = (max, min) => {
        const number = Math.floor(Math.random() * 10)
        return number

    }
    return (
        <div style={{ display: "flex" }}>
            {choices.map((val, idx) => {
                return (
                    <button className="btn fourth answerButton" onClick={() => props.handleClick(val)}>
                        {val}
                    </button>
                )
            })}

        </div>
    )
}
